.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 36px;
    left: 32px;
    top: 32px;
}

/* color  */
.bm-burger-bars {
    background: #373a47;
}

/* color to our buger icon on hover */
.bm-burger-bars-hover {
    background: #a90000;
}

/* size of cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
}

/* color of cross button */
.bm-cross {
    background: #bdc3c7;
}

/* sidebar wrapper styles */
.bg-menu-wrap {
    position: fixed;
    height: 100%;
    width: 300px;
    overflow-x: hidden;
    box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
}

.bm-item-list {
    padding: 0.8em;
}

.bm-menu {
    background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
    padding: 2.5em 1.5em 0 0;
    height: 100vh;
    overflow: hidden;
}

.menu-item {
    position: relative;
    font-size: 2em;
    text-decoration: none;
    color: #fff;
    font-family: monstserrat;
    font-style: normal;
    font-weight: normal;
    padding: 10px;
}

.menu-item:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}
.menu-item-before:hover,
.active-item {
    font-weight: 500;
}
.active-item:before {
    visibility: visible;
    height: 3px;
}